/* 柱状图 */
.map-sign {
  position: absolute;
  width: 20px;
  height: 1px;
  cursor: pointer;
  z-index: 10;
}
.map-sign:before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  width: 37px;
  height: 10px;
  background: url(../../../../static/css/images/map/shadow.png);
  background-size: 100% 100%;
}
.map-sign .map-sign-bar {
  position: absolute;
  width: 20px;
  height: 10px;
  bottom: 0;
  left: -5px;
  padding: 12px 0 8px 0;
  min-height: 24px;
  z-index: 2;
}
.map-sign .map-sign-bar:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 8px;
  width: 100%;
}
.map-sign .map-sign-bar:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 12px;
  width: 100%;
}
.map-sign .map-sign-num {
  position: absolute;
  top: -24px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 18px;
  line-height: 18px;
  border-radius: 2px;
  text-align: center;
  min-width: 26px;
  padding: 0 2px;
  font-size: 20px;
  font-weight: 700;
  color:rgba(0,0,0,0.7);
  width: 150px;
}
.map-sign .map-sign-text {
  position: absolute;
  bottom: -20px;
  left: 50%;
  margin-left: -20%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 18px;
  line-height: 18px;
  border-radius: 2px;
  text-align: center;
  min-width: 250px;
  font-size: 17px;
  font-weight: 700;
  color:rgba(0,0,0,0.7)
}
