@-webkit-keyframes circle-scale {
0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 1;
}
90% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
}
}
@keyframes circle-scale {
0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 1;
}
90% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
}
}
