
.tableClass{
    width: 1250px;
    border: 1px solid #333;
    border-collapse:collapse;
    margin-top: 20px;
    margin-bottom: 20px;
}
.tableClass tr td{
    height: auto;
    min-height: 44px;
    line-height: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    border:1px solid #333;
    font-size: 16px;
}
.tableClass tr th{
    height: auto;
    min-height: 44px;
    line-height: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    border:1px solid #333;
    font-size: 16px;
    font-weight: bolder;
}




