
.tab-border{
    table-layout: fixed;width:950px;word-wrap:break-word;border: 1px solid black;text-align:center;
}
.tab-border  tr td{
    border: 1px solid black
}
.tab-border  th {
    border: 1px solid black
}
.img_container{
    position: relative;
    padding: 0px;
    width: 150px;
    height: 200px;
    margin-right: 20px;
    background: #fff;
    border: 1px solid #F5F5F5;
    -webkit-box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.09);
            box-shadow: 0 0 6px 2px rgba(0, 0, 0, 0.09);
    border-radius: 4px;
}
.img_container img{
    width: 150px;
    height: 200px;
    border-radius: 4px;
}
.img_container .mask{
    position: absolute;
    top:0;
    left: 0;
    width: 150px;
    height: 200px;
    background: rgba(101, 101, 101, 0.6);
    color: #ffffff;
    line-height: 200px;
    text-align:center;
    border-radius: 4px;
    display: none;
    white-space: nowrap;
    cursor: pointer;
}
