
.smsPush .el-table{
	margin-top: 1%;
}
.smsPush .el-card{
	width: 100%;
	border: 0;
}
.smsPush .el-card__body{
	
	-webkit-box-sizing: border-box;
	
	        box-sizing: border-box;
}
.smsPush .el-table th{
	padding: 15px 5px;
  font-size: 16px;
  background-color: rgba(223, 234, 251, 0.3);
	color: #333;
}
.smsPush .messageMain .el-dialog__wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.smsPush .messageMain  .el-dialog{
	margin: 0!important;
	width: auto;
	border-radius: 5px;
}
.smsPush .messageMain .el-dialog__header{
	border-radius: 4px 4px 0 0;
}
.smsPush .el-table th.is-leaf, .el-table td{
	border: 0;
}
.smsPush .el-table::before{
	height: 0;
}



