
.el-table tr th {
  border-bottom: 0px;
  padding: 15px 5px;
  font-size: 16px;
  background-color: rgba(223, 234, 251, 0.3);
	color: #333;
}
.el-date-editor .el-range-separator{
  width: 10%;
}
.el-table th.is-leaf, .el-table td{
	border: 0;
}
.el-table::before{
	height: 0;
}
